<!-- 用户登录界面 -->
<template>
  <div id="login">
    <div class="bg"></div>
    <el-row class="main-container">
      <el-col :lg="8" :xs="16" :md="10" :span="10">
        <div class="bottom">
          <div class="bg-color"></div>
          <div class="container">
            <el-page-header
              @back="goBack"
              title="返回登录"
              class="back"
            ></el-page-header>
            <el-form
              :label-position="labelPosition"
              label-width="80px"
              ref="formLabelAlign"
              :model="formLabelAlign"
              :rules="rules"
            >
              <el-form-item label="姓名" prop="studentName" :label-width="formLabelWidth">
                <el-input
                  v-model="formLabelAlign.studentName"
                  placeholder="请输入姓名"
                ></el-input>
              </el-form-item>
              <el-form-item label="性别" prop="sex" :label-width="formLabelWidth">
                <el-select
                  v-model="formLabelAlign.sex"
                  placeholder="请选择性别"
                >
                  <el-option label="男" value="男"></el-option>
                  <el-option label="女" value="女"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="密码" prop="pwd" :label-width="formLabelWidth">
                <el-input
                  v-model="formLabelAlign.pwd"
                  placeholder="请输入密码"
                  type="password"
                ></el-input>
              </el-form-item>
              <el-form-item label="确认密码" prop="confirmPwd" :label-width="formLabelWidth">
                <el-input
                  v-model="formLabelAlign.confirmPwd"
                  placeholder="请再次输入密码"
                  type="password"
                ></el-input>
              </el-form-item>
              <el-form-item label="专业" prop="major" :label-width="formLabelWidth">
                <el-select
                  v-model="formLabelAlign.major"
                  placeholder="请选择专业"
                >
                  <el-option label="计算机科学与技术" value="计算机科学与技术"></el-option>
                  <el-option label="信息工程" value="信息工程"></el-option>
                  <el-option label="网络工程" value="网络工程"></el-option>
                  <el-option label="软件工程" value="软件工程"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="年级" prop="grade" :label-width="formLabelWidth">
                <el-select
                  v-model="formLabelAlign.grade"
                  placeholder="请选择年级">
                  <el-option label="2019" value="2019"></el-option>
                  <el-option label="2020" value="2020"></el-option>
                  <el-option label="2021" value="2021"></el-option>
                  <el-option label="2022" value="2022"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="班级" prop="clazz" :label-width="formLabelWidth">
                <el-select
                  v-model="formLabelAlign.clazz"
                  placeholder="请选择班级"
                >
                  <el-option label="1班" value="1"></el-option>
                  <el-option label="2班" value="2"></el-option>
                  <el-option label="3班" value="3"></el-option>
                  <el-option label="4班" value="4"></el-option>
                  <el-option label="5班" value="5"></el-option>
                  <el-option label="6班" value="6"></el-option>
                  <el-option label="7班" value="7"></el-option>
                  <el-option label="8班" value="8"></el-option>
                  <el-option label="9班" value="9"></el-option>
                  <el-option label="10班" value="10"></el-option>

                </el-select>
              </el-form-item>
              <el-form-item label="联系方式" prop="tel" :label-width="formLabelWidth">
                <el-input
                  v-model="formLabelAlign.tel"
                  placeholder="请输入联系方式"
                ></el-input>
              </el-form-item>
              <el-form-item label="email" prop="email" :label-width="formLabelWidth">
                <el-input v-model="formLabelAlign.email" placeholder="请输入邮箱"></el-input>
              </el-form-item>
              <el-form-item label="身份证号" prop="cardId" :label-width="formLabelWidth">
                <el-input
                  v-model="formLabelAlign.cardId"
                  placeholder="请输入身份证号"
                  @blur="check()"
                ></el-input>
              </el-form-item>
              <div class="submit">
                <el-button
                  type="primary"
                  class="row-register"
                  @click="register('formLabelAlign')"
                  >注册</el-button
                >
              </div>
            </el-form>
          </div>
        </div>
      </el-col>
    </el-row>
    <el-row class="footer">
      <el-col>
        <p class="msg2">版权所有 ©2023 聊城大学计算机学院软工4班张龙祥
          保留所有权利
          <a href="http://gat.shandong.gov.cn/" style="color:#ff962a "
             target="_blank">鲁ICP备19001371号</a>
        </p>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import store from "@/vuex/store";
import { mapState } from "vuex";
export default {
  store,
  name: "login",
  data() {
    var confirm = (rule, value, callback) => {
      if (value === undefined) {
        callback(new Error("请再次输入密码"));
      } else if (value != this.formLabelAlign.pwd) {
        callback(new Error("两次输入的密码不一致"));
      } else {
        callback();
      }
    };
    return {
      role: "1",
      labelPosition: "left",
      confirmPwd: "",
      formLabelWidth: "88px",
      formLabelAlign: {
        studentName: "",
        pwd: "",
        grade: "",
        major: "",
        clazz: "",
        institute: "计算机学院",
        tel: "",
        email: "",
        cardId: "",
        role: "2",
        sex: ""
      },
      rules: {
        studentName: [
          { required: true, message: "姓名不能为空", trigger: "blur" }
        ],
        pwd: [
          { required: true, message: "密码不能为空", trigger: "blur" },
          { min: 6, message: "密码不能少于6位字符", trigger: "blur" }
        ],
        confirmPwd: [{ validator: confirm, trigger: "blur" }],
        major: [
          { required: true, message: "请选择您的专业", trigger: "change" }
        ],
        grade: [
          { required: true, message: "请选择您的年级", trigger: "change" }
        ],
        clazz: [
          { required: true, message: "请选择您的班级", trigger: "change" }
        ],
        tel: [{ required: true, message: "联系方式不能为空", trigger: "blur" }],
        email: [
          { required: true, message: "邮箱不能为空", trigger: "blur" },
          { type: "email", message: "邮箱格式不正确", trigger: "blur" }
        ],
        cardId: [
          { required: true, message: "身份证号不能为空", trigger: "blur" },
          {min:18, max: 18, message: "请输入正确的身份证号", trigger: "blur" }
        ],
        sex: [{ required: true, message: "请选择您的性别", trigger: "change" }]
      }
    };
  },
  methods: {
    check() {
      this.$axios(`/api/scheck/${this.formLabelAlign.cardId}`)
        .then(res => {
          if (res.data.code == 404) {
            this.$message({
              showClose: true,
              type: "error",
              message: res.data.message
            });
          }
        })
        .catch(error => {});
    },
    register(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$axios({
            url: `/api/sregister`,
            method: "post",
            data: {
              ...this.formLabelAlign
            }
          }).then(res => {
            if (res.data.code == 200) {
              this.$message({
                showClose: true,
                type: "success",
                message:
                  res.data.message +
                  "，当前用户信息已与账号 " +
                  res.data.data +
                  " 绑定，请登录"
              });
              this.$router.push({
                name: "login",
                params: {
                  username: res.data.data
                }
              });
            } else {
              this.$message({
                showClose: true,
                type: "error",
                message: res.data.message
              });
            }
          });
        } else {
          return false;
        }
      });
    },
    goBack() {
      this.$router.push("/");
    }
  },
  computed: mapState(["userInfo"]),
  mounted() {}
};
</script>

<style lang="scss" scoped>
.container {
  margin-top: 15px;
  margin-bottom: 32px;
}
.container .el-radio-group {
  margin: 30px 0px;
}
a:link {
  color: #ff962a;
  text-decoration: none;
}
#login {
  font-size: 14px;
  color: #000;
  background-color: #fff;
}
#login .bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  overflow-y: auto;
  height: 100%;
  background: url("../../assets/img/login-bg.jpg") center top / cover no-repeat;
  background-color: #b6bccdd1 !important;
}
#login .main-container {
  display: flex;
  justify-content: center;
  align-items: center;
}
#login .bottom {
  display: flex;
  justify-content: center;
  background-color: #fff;
  background-color: transparent !important;
  border-radius: 5px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  z-index: 1;
  position: relative;
  margin-top: 30px;
}
#login .back {
  margin-top: 5px;
  margin-bottom: 10px;
  color: #000;
  height: 30px;
  font-size: 30px;
}
.bg-color {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #f1f1f1;
  opacity: 0.3;
  z-index: -1;
}
#login .bottom .title {
  text-align: center;
  font-size: 30px;
}
.bottom .submit .row-register {
  width: 100%;
  background-color: #ff962a;
  border-color: #ff962a;
  margin: 10px 0px 10px 0px;
  padding: 15px 20px;
}
.bottom .submit {
  display: flex;
  justify-content: center;
}
.footer {
  margin-top: 46px;
  text-align: center;
}
.footer .msg1 {
  font-size: 18px;
  color: #fff;
  margin-bottom: 15px;
}
.footer .msg2 {
  font-size: 14px;
  color: #e3e3e3;
  margin-top: 0px;
}
/deep/ .el-form--label-left .el-form-item__label {
  text-align: right;
  font-weight: 550;
  font-size: 16px;
  color: #000;
}
/deep/ .el-page-header__title {
  font-size: 16px;
  font-weight: 550;
  color: #ff962a;
}
/deep/ .el-page-header__left {
  color: #ff962a;
  height: 24px;
}
/deep/ .el-form-item__error {
  color: #ff962a;
  font-size: 12px;
  font-weight: 550;
}
</style>‘  ’



